      "author": "Bristol Calvert",
      "year": "2012",
      "abstract": "Recibieron asesoramiento estandarizado del farmacéutico del estudio sobre la importancia de la adherencia a sus medicamentos cardiovasculares y la revisión del propósito de cada medicamento. En coordinación con el médico y el personal del hospital, el farmacéutico del estudio abordó las barreras identificadas para la adherencia a la medicación. Se proporcionó una tarjeta de bolsillo para medicamentos, una lista de consejos para recordar tomar los medicamentos y un pastillero. Antes del alta hospitalaria, el farmacéutico del estudio se puso en contacto con la farmacia comunitaria específica del paciente y discutió las barreras identificadas para la adherencia a la medicación con el farmacéutico comunitario. El farmacéutico del estudio llamó a cada paciente de 1 a 2 semanas después del alta hospitalaria para verificar que el paciente había surtido todas las recetas de alta .Cuando un medicamento no se llenó a tiempo o no se interrumpió, se le pidió al farmacéutico que le explicara el motivo al paciente y que se pusiera en contacto con el médico sobre la falta de cumplimiento según fuera necesario. Las intervenciones realizadas por el farmacéutico de la comunidad se documentaron y enviaron por fax a los investigadores del estudio. Ambos grupos de tratamiento recibieron consejería de alta de rutina realizada por la enfermera de atención al paciente y una carta / resumen de alta del médico del hospital al médico de la comunidad que enumera los medicamentos, procedimientos y recomendaciones de alta.",
