      "author": "Edworthy",
      "year": "2007",
      "abstract": "Los pacientes del grupo de intervención recibieron asesoramiento individual y grupal relacionado con sus medicamentos y afecciones. Se utilizaron videos y materiales impresos para promover la adherencia a las terapias a largo plazo. Además, la medición posterior al alta de los niveles de lípidos séricos (el estándar en la región en ese momento) se organizó cuando fue apropiado. Se enviaron mapas de atención farmacéutica, incluidos planes a largo plazo, a los médicos de familia y farmacéuticos de los pacientes. Una semana después del alta, un farmacéutico del estudio se puso en contacto con los pacientes para revisar sus medicamentos y ayudarlos con los problemas relacionados con la medicación. Posteriormente, una enfermera del estudio realizó un programa de contactos al mes, trimestralmente durante 12 meses y luego seis meses. No hubo restricción de tiempo para las interacciones telefónicas. Las enfermeras y los farmacéuticos se aseguraron de que las recetas fueran seguidas y determinaron si había problemas relacionados con la medicación. Es importante señalar que el personal también facilitó la resolución de cualquier otro problema médico encontrado. Farmacéuticos comunitarios asistidos por el asesoramiento activo de los pacientes del grupo de intervención únicamente.",
