      "author": "Furuya",
      "year": 2014,
      "abstract": "Los participantes del grupo de intervención recibieron la información entregada de forma rutinaria por la institución al alta más un programa educativo con seguimiento telefónico. Programa educativo basado en el constructo de la autoeficacia según la Teoría Cognitiva Social de Albert Bandura (Bandura 2004). El programa educativo consistió en tres folletos titulados Angioplastia coronaria transluminal percutánea, Regreso a casa después de la angioplastia coronaria y Cómo cuidar su corazón y su salud, y seguimiento telefónico. El guión telefónico contenía preguntas sobre el cuidado personal que incluían: cuidado del brazo y la pierna utilizados para el procedimiento de PCI, cambios en los factores de riesgo de CAD y uso correcto de la medicación. Los folletos y el guión telefónico fueron desarrollados por dos de los investigadores. Un diseñador de arte visual ilustró los folletos. Los contenidos del programa educativo se seleccionaron en base a la literatura, especialmente los manuales My Heart, My Life (National Heart Foundation of Australia 2008) y The Heart Manual (Lothian Health Board 2007). El guión telefónico se desarrolló de acuerdo con el contenido de los folletos. La implementación del programa se realizó mediante instrucción individual (Bastable 2010) por dos de los investigadores (RKF, ECA). El primer folleto fue presentado y discutido con los participantes antes de someterse al procedimiento de PCI.. Los otros dos folletos se centraron en el autocuidado relacionado con la PCI en sí y el manejo diario de la enfermedad, que fueron discutidos con los participantes después de la ICP, el día del procedimiento o al día siguiente. Se realizaron tres llamadas telefónicas (en la primera, octava y decimosexta semanas después del alta hospitalaria), centradas en cambios en el estilo de vida. Las llamadas fueron realizadas por los mismos investigadores que presentaron y discutieron los folletos con los participantes durante su hospitalización. A cada participante se le preguntó si estaba ejecutando con éxito cambios en el estilo de vida.. También se preguntó a los participantes sobre su conocimiento sobre el uso adecuado de la medicación prescrita. A cada participante se le indicó que hiciera una llamada telefónica al investigador para obtener más preguntas o apoyo para la prevención secundaria de la EAC.",
