      "author": "Lourenco",
      "year": 2013,
      "abstract": "La intervención se basó en estrategias de planificación de acción y afrontamiento, de acuerdo con los principios teóricos de la intención de implementación una estrategia de autorregulación que empareja respuestas concretas a posibles situaciones futuras. Nuestra intervención consistió en dos reuniones cara a cara (al inicio del estudio y una más tarde, durante la visita de la persona a la clínica) y dos llamadas telefónicas (15 y 45 días después del inicio). Con la ayuda de uno de los autores, en la entrevista inicial, los participantes del grupo de intervención elaboraron hasta tres planes de acción sobre cuándo, dónde y cómo pretendían tomar la medicación prescrita para la cardioprotección y el alivio de los síntomas de la enfermedad. De manera similar, los participantes identificaron hasta tres barreras para tomar la medicación. Luego, se identificaron una o más estrategias para superar cada barrera, para lograr la estrategia de planificación de afrontamiento. El número de barreras se limitó a tres porque muchos de los participantes eran adultos mayores y tendrían dificultades para memorizar demasiadas estrategias .Los planes se reforzaron mediante dos llamadas telefónicas. Se hicieron las siguientes preguntas: (1) ¿Ha estado tomando la medicación recetada por su médico para el tratamiento de su enfermedad cardíaca ?; (2) ¿Ha tenido dificultades para tomar la medicación prescrita ?; (3) ¿Podríamos recordar juntos los planes que se elaboraron para superar esos problemas ?; y (4) Pensemos juntos sobre qué estrategias pueden ayudarlo a superar esos problemas.",
