      "author": "Mosca",
      "year": 2010,
      "abstract": "El grupo de intervención recibió educación y asesoramiento por un facilitador de prevención = educador durante la hospitalización y durante las visitas telefónicas a las 2, 4 y 12 semanas y una visita telefónica o presencial a las 6 semanas posteriores al alta. Los facilitadores no eran médicos ni enfermeras profesionales  pero tenían formación formal en educación para la salud, y habían recibido  entrenamiento estandarizado por el Programa de Cardiología Preventiva Hospitalaria. La sesión educativa inicial incluyó 1 hora de asesoramiento psicológico previo al alta y proporcionó información sobre cómo lograr las metas basado en la teoría del aprendizaje social y el constructo de las cinco A (pregunte, asesorar, acordar, ayudar, organizar). Específicamente, después de completar realización de una evaluación de meta de prevención secundaria, los participantes recibieron retroalimentación sobre sus objetivos y comportamientos de salud. Se les aconsejó sobre recomendaciones para cambio de comportamiento personalizado a sus necesidades específicas. Los facilitadores y participantes acordaron sobre objetivos específicos y metas  alcanzables. Los facilitadores ayudaron a los participantes a desarrollar acciones planes de acción para lograr sus objetivos y proporcionar referencias como necesario. Fechas y horarios para visitas de seguimiento y llamadas telefónicas se organizaron al final de la sesión educativa inicial para el propósito de obtener retroalimentación, monitorear el progreso y Fomentar la acción preventiva. Se informó a todos los sujetos que el uso de beta-bloqueadores, aspirina o agentes antiplaquetarios e inhibidores de la ECA a menudo se recomienda para pacientes con cardiopatía coronaria, y los criterios para su uso, Se revisaron las contraindicaciones. Antes del alta, cada sujeto revisó un Lista de verificación del estado de la meta de prevención con el equipo de prevencióny recibió un folleto educativo en inglés o Español que discutió cada objetivo en detalle, incluido el justificación de la modificación de los factores de riesgo y estrategias para lograr cada objetivo. Los médicos de los participantes asignados al azar al grupo de intervención recibieron  informes de progreso estandarizados sobre consecución de objetivos personalizados al alta y a las 6 semanas. Los informe incluyó el nivel actual de control de factores de riesgo y uso de medicamentos, así como objetivos mínimos y óptimos.",
