      "author": "Zhao",
      "year": 2008,
      "abstract": "El TCP constaba de dos fases, predescarga y después del alta. La enfermera del Hospital Central (llamada Enfermera-H a continuación) fue responsable de la fase previa al alta. Dos enfermeras contratadas de un hospital comunitario (llamado Enfermera-C a continuación) fueron responsables de la postratamiento fase. Las tres enfermeras fueron capacitadas para realizar la programa, de acuerdo con un conjunto de protocolos. Las áreas clave que debían abordarse en el protocolo eran los conocimientos de los participantes comportamientos de pie y adherencia con respecto a la dieta, medicamentos, ejercicio y estilo de vida relacionado con la salud, descansar lo suficiente y dejar de fumar. En la fase previa al alta, la enfermera-H evaluó al participante basado en el conjunto de protocolos y proporcionó asesoramiento sanitario a los clientes. Ella documentaría los consejos dados y los áreas que necesitaban seguimiento en un informe que se envió a Enfermera-C. Según el informe de derivación, la Enfermera-C continuó seguimiento con el participante en la comunidad durante cuatro semanas. La enfermera C proporcionó una visita domiciliaria el segundo día y otro en la tercera semana e hizo dos llamadas telefónicas en la segunda y cuarta semanas después de que el paciente fue dado de alta del hospital. El seguimiento también fue establecido en el protocolo y la Enfermera-C establecería metas con el participante en el final de cada interacción según las necesidades individuales de los participantes. En la siguiente visita / llamada telefónica, La enfermera C evaluó si el participante había logrado el objetivos de salud anteriores, proporcionó educación y asesoramiento como apropiado y luego establecer nuevos objetivos de salud.",
