      "author": "Cao",
      "year": 2017,
      "abstract": "El programa de transición de asociación entre el hospital y la comunidad fue realizado por la combinación de un cardiólogo y una enfermera del hospital, así como un médico de familia y una enfermera a domicilio en cada centro de salud comunitario al que pertenecía cada paciente. La disposición del programa fue la siguiente. Fase de admisión El cardiólogo y la enfermera del hospital evaluaron simultáneamente los medicamentos previos al ingreso de los pacientes. El cardiólogo ajustó estos medicamentos si era necesario e informó a los pacientes de inmediato, mientras que la enfermera del hospital registró cualquier alergia. Fase de hospitalización El cardiólogo era responsable de proporcionar a los pacientes instrucciones sobre su diagnóstico, plan de tratamiento, medicamentos, riesgos y beneficio del tratamiento. La enfermera del hospital distribuyó un folleto escrito de autocuidado y brindó consejos sobre el autocuidado de la enfermedad a cada paciente y sus cuidadores. Este consejo incluía instrucciones sobre su medicación, dieta, ejercicios, manejo del estado de ánimo, etc. Se llevó a cabo un método de enseñanza para reforzar la comprensión y comprensión del paciente. Fase previa al alta El cardiólogo, la enfermera del hospital, el paciente y sus cuidadores desarrollaron un plan de alta por escrito e individualizado en cooperación el día antes de su alta. El contenido incluía la dirección y el número de teléfono de cada paciente, el plan de diagnóstico y tratamiento, los medicamentos posteriores al alta, los arreglos de seguimiento posteriores al alta, la información de contacto de los centros de salud comunitarios y la dieta y los ejercicios posteriores al alta, etc. , betabloqueantes, ácido acetilsalicílico, inhibidores de la enzima convertidora de angiotensina, bloqueadores del receptor de angiotensina II y antiagregantes plaquetarios distintos del ácido acetilsalicílico, fue distribuido y explicado a cada paciente por el cardiólogo. La planificación del alta también se instruyó mediante el uso de una estrategia de enseñanza. Fase posterior al alta La enfermera del hospital envió el plan de alta por correo electrónico a la enfermera a domicilio en cada centro de salud comunitario al que pertenecía cada paciente dentro de las 48 h posteriores a su alta. Luego, la enfermera domiciliaria estableció un registro de salud electrónico para el paciente y notificó a su médico de familia sobre el seguimiento posterior al alta. Durante la primera semana después de su alta, el médico de familia y la enfermera domiciliaria realizaron llamadas telefónicas estructuradas al paciente. El médico de familia era responsable de monitorear el progreso de la enfermedad, ajustar el plan de tratamiento si era necesario y evaluar la necesidad de derivación. La información médica del paciente o el registro de progreso en la fase posterior al alta se transmitieron a la enfermera del hospital por correo electrónico con un mensaje corto de notificación si el paciente debía ser readmitido en el hospital. La enfermera domiciliaria reforzó los comportamientos de autogestión de la salud y revisó y restableció los objetivos de atención médica con el paciente. Durante la cuarta semana después del alta, además de las mismas tareas que tanto el médico de familia como la enfermera domiciliaria debían realizar como en la primera semana, la enfermera domiciliaria debía recordar al paciente las visitas ambulatorias oportunas al hospital. Los servicios, incluido el asesoramiento básico de salud y el recordatorio al paciente de las visitas ambulatorias oportunas al hospital, fueron realizados por una enfermera del hospital 1 y 4 semanas después de su alta, respectivamente.",
