      "author": "Dale",
      "year": 2015,
      "abstract": "Los participantes recibieron 7 mensajes por semana (1 por día) y tuvieron acceso a un sitio web de apoyo. Los participantes de la intervención también recibieron un podómetro para autocontrolar su actividad física. Los mensajes se adaptaron al nombre de los participantes y la hora del día preferida para recibirlos. De la semana 13 a la 24, la frecuencia de los mensajes disminuyó a 5 por semana. Se utilizó la mensajería bidireccional porque se pidió a los participantes que enviaran mensajes de texto en el conteo semanal de pasos del podómetro y que hicieran preguntas o retroalimentaran sobre otros comportamientos. Las respuestas a los recuentos de pasos se automatizaron y se basaron en el número de pasos logrados, mientras que el equipo de investigación respondió personalmente a las preguntas individuales en un plazo de 48 horas. Se reembolsó a los participantes los costos asociados con la mensajería de texto",
