      "author": "Sheridan",
      "year": 2013,
      "abstract": "intervención incluyó una combinación de estrategias para mejorar el estilo de vida y la adherencia a la medicación entregada en dos formatos alternativos: proporcionado por un consejero o basado en la web. Participantes recibieron una ayuda para la toma de decisiones computarizada y luego 7 sesiones de asesoramiento de un consejero o 7 sesiones de interacción interactiva mensajería personalizada en la web (hasta 5,5 h de intervención contacto; Al diseñar la intervención, nuestro objetivo era entregar el mismo contenido en ambos formatos. Así, diseñamos el asesoramiento con guión y materiales escritos en el brazo del consejero para que coincida con el texto de la intervención basada en la web y utilizó el misma secuenciación de materiales para ambas intervenciones.", 
