      "author": "Park",
      "year": 2013,
      "abstract": "La intervención incluyó recordatorios de MT para tomar antiplaquetarios y medicamentos con estatinas. Durante los 30 días de participación, tanto Los grupos experimentales recibieron MT de un programa personalizable. a través del administrador de salud móvil de CareSpeak Communications plataforma (Nueva Jersey). El grupo TM Reminders + TM Education recibió recordatorios personalizados de TM que se entregaron a veces seleccionados por pacientes que se correlacionaban con su horario de medicación es decir, recordatorio antiplaquetario por la mañana y recordatorio de estatinas en la noche). El grupo TM Reminders + TM Education recibió 74 mensajes durante los 30 días, mientras que el grupo TM Educación recibió 14 mensajes. El número total de MT incluyó una MT inicial para invitar a los pacientes a participar seguido de una segunda MT para proporcionar información sobre cómo darse de baja después de que el paciente aceptó participar. Los recordatorios de medicación eran bidireccionales, requiriendo que los pacientes respondan para confirmar la recepción. Un ejemplo de El recordatorio de la medicación era 'John, toma Plavix 75 mg a las 9:00 a. m.'.Responda con 1. Ambos grupos experimentales recibieron educación unidireccionalmensajes de salud sobre la reducción del riesgo cardiovascular el lunes, Miércoles y viernes a las 14 h. El propósito de la TM de salud fue para determinar si la educación del paciente, entregada tres veces al día semana, mejoraría la adherencia a la medicación en lugar de la administración directa recordatorios de medicamentos, entregados dos veces al día. Un ejemplo de el mensaje de educación sanitaria fue: 'Recuerde ver su cardiólogo y / o médico de cabecera 1 a 2 semanas después de su hospitalización'. Se llamó a los pacientes cerca del final de sus 30 días participación para programar una visita de seguimiento, en cuyo momento tres cuestionarios relacionados con la adherencia a la medicación y la autoeficacia se repitieron.",
      "id": 15
